@import './reset';
@import './color';
@import './text.scss';


html{
    display: block;
    height: 100%;
  }
body {
    background-color: $bg-color;
    font-family: 'Open Sans', sans-serif;
    color: $p-color;
    line-height: 1.4;
}
.hide {
    display: block;
}
.mobile-title {
    display: none;
}
.h3-hide-title {
    display: none;
}
/************** NAV **************/
.flex-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 1rem;
}
nav {
    width: 100%;
    margin-bottom: 200px;
}
.index-container {
    margin-top: 450px;
}
.nav-link {
    text-transform: uppercase;
    padding: 0 3rem;
    border-right: 1px solid $link-color;
}

.last-in-line {
    border-right: none;
}


/**************LINKS**************/

a:link {
    font-weight: 600;
    color: lighten($p-color, 4%);
    text-decoration: none;
  }

  a:visited {
    color: $p-color;
  }

  a:hover {
    color: $h1-color;
    text-shadow: 2px 2px 2px $title-shadow;
  }

  a:active {
    color: $h1-color;
    text-shadow: 1px 1px 1px $title-shadow;
  }

/**************IMAGES**************/
.icon-img {
    width: 30px;
}
.project_img {
    position: relative;
    z-index: 10;
    width: 250px;
    height: 250px;
    border-radius: 250px;
    opacity: 0.5;
}

/************** XP *****************/
#xp-list {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    ul {
        display: flex;
        justify-content: center;
    }

li {
    display: inline;
    padding: 3%;
    margin: 2%;
    border-radius: 5px;
    box-shadow: 3px 1px 5px $title-shadow;
    //background-color: $triangle-color;
    color: $h1-color;
    font-weight: 600;
    text-shadow: $title-shadow 1px 1px, $title-shadow 1px 1px, $title-shadow 2px 2px;
}
li:hover {
    box-shadow: 2px 1px 3px $title-shadow;
    color: lighten($h1-color, 8%);
    cursor: pointer;
}
li:active {
    box-shadow: 1px 1px 1px $title-shadow;
    color: $h1-color;
}
.animate__animated.animate__backInRight.css {
    --animate-duration: 1.2s;
}
.animate__animated.animate__backInRight.javascript {
    --animate-duration: 1.4s;
}
.animate__animated.animate__backInRight.php {
    --animate-duration: 1.6s;
}
.animate__animated.animate__backInRight.wordpress {
    --animate-duration: 1.8s;
}
.animate__animated.animate__backInRight.c {
    --animate-duration: 2s;
}
}
/**************COURSES**************/

.courses {
    
    font-weight: 600;
    display: block;
    display: flex;
    justify-content: center;
    td {
        vertical-align: middle;
        padding-left: 1rem;
        width: 67%;
        &:nth-child(1){
            border-right: 1px solid rgba($table-line-color, 0.5);
            padding-right: 1rem;
            width: 33%;
        }
    }
    tr {
        height: 40px;
        border-bottom: 1px solid rgba($table-line-color, 0.5);
    }
    table {
        width: 455px;
    }
    
}
.table-2 {
    margin-top: 5rem;
}
.course-list {
    padding-top: 1rem;
    padding-bottom: 2rem;
    line-height: 2;
}

    td.courses-read {
        vertical-align: top;
        padding-top: 1rem;
        line-height: 2;
    }
    

tr.last-in-line {
    border-bottom: none;
}
.courses-left {
    padding-left: 1rem;
    max-width: 300px;
    ul {
        list-style: disc;

    }
    
}
/**************PROJEKT**************/
.projects {
    display: block;
    margin: auto;
    width: 100%;
    max-width: 1000px;
}
.project-list {
    float: left;
    width: 31.3%;
    height: 350px;
    text-align: center;
    margin: 1%;
    background-color: $triangle-color;
    padding: 1rem;
    box-shadow: 1px 1px 10px $title-shadow;
    img:hover {
        opacity: 1;
    }
    h2 {
        font-weight: 600;
        line-height: 2;
        font-size: 1.5rem;
        color: $h1-color;
        background-color: $triangle-color;
        text-shadow: $title-shadow 1px 1px, $title-shadow 2px 2px, $title-shadow 3px 3px;
        a {
            color: $h1-color;
        }
    }
    h2:hover {
        text-shadow: none;
    }
    p {
        position: relative;
        bottom: 30px;
        font-weight: 600;
    }
    ul {
        list-style: none;
    }
}
.project-list:hover {
    box-shadow: none;
}
.project-text {
    position: relative;
    z-index: 100;
    bottom: 145px;
}

/**************STORY START**************/

.my-img {
    display: block;
    margin: auto;
    border: 5px solid $h1-color;
    border-radius: 1px 150px 1px 150px;
}
.arrow-right {
    display: block;
    margin: auto;
    padding-top: 1rem;
    width: 250px;
    opacity: 0.8;
}

.arrow-right:hover {
    opacity: 1;
}
/**************MY STORY**************/
.top-boxes {
    position: absolute;
    display: block;
    margin: auto;
    width: 900px;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 300px;
    h3 {
        text-transform: lowercase;
        font-family: 'Righteous', cursive;
        line-height: 2;
        font-size: 1.5rem;
        font-weight: 600;
        color: $h1-color;
        text-shadow: $title-shadow 1px 1px, $title-shadow 2px 2px, $title-shadow 3px 3px;
    }
}
.bottom-boxes {
    @extend .top-boxes;
    top: 990px;
}
.orange-box {
    position: absolute;
    left: 22%;
    top: 30px;
    width: 500px;
    height: 180px;
    background-color: $h1-color;
    opacity: 0.8;
    z-index: 1;
}
.light-box {
    position: absolute;
    left: 26%;
    padding: 1rem;
    padding-top: 0;
    padding-right: 6%;
    width: 500px;
    height: 180px;
    background-color: $boxcolor-light;
    z-index: 10;
    font-size: 14px;
}
.middle-boxes {
    position: absolute;
    display: block;
    margin: auto;
    width: 900px;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 750px;

    .left-middle-box {
        float: left;
        height: 400px;
        width: 48%;
        margin-right: 2%;
        background-color: $boxcolor-dark;
    }
    .right-middle-box {
        float: left;
        height: 400px;
        width: 48%;
        margin-left: 2%;
        border: 1px solid rgba($table-line-color, 0.8);
    }

}
.triangle-topleft {
    position: relative;
    width: 0;
    height: 0;
    top: 4%;
    left: 4%;
    border-top: 200px solid $triangle-color;
    border-right: 200px solid transparent;
  }

  .triangle-bottomright {
    position: relative;
    width: 0;
    height: 0;
    top: 46%;
    left: 49%;
    border-bottom: 200px solid $boxcolor-light;
    border-left: 200px solid transparent;
  }
.middle-box-text {
    position: absolute;
    display: block;
    margin: auto;
    top: 100px;
    width: 100%;
    max-width: 450px;
    h3 {
        text-transform: lowercase;
        font-family: 'Righteous', cursive;
        line-height: 2;
        font-size: 1.5rem;
        padding-left: 9%;
        font-weight: 600;
        color: $h1-color;
        text-shadow: $title-shadow 1px 1px, $title-shadow 2px 2px, $title-shadow 3px 3px;
    }

    p {
        padding-left: 9%;
        color: $p-color-light;
        font-size: 14px;
        font-weight: 600;
        padding-right: 5%;
    }
}
.right-middle-box p{
    
    color: $p-color;
    padding-left: 7%;
    
}
.right-middle-box h3 {
        padding-left: 7%;
}
#work li {
    color: $p-color;
    margin-left: 7%;
    font-size: 14px;
    font-weight: 600;
    padding-right: 5%;
    list-style-type: disc;

}
b {
    color: darken($p-color, 4%);
}
.social-media {
    padding-top: 2rem;
    padding-left: 3rem;
    p {
        display: block;
        margin: 0.5rem;
        overflow: auto;

    }
}
.social-media span {
    float: left;
    clear: right;
    padding-top: 5px;
    margin-left: 1rem;
    font-size: 16px;
}
.social-media img {
    float: left;
    clear: left;
}


/**************FOOTER**************/
footer {

    text-align: center;

.logo {
    width: 100%;
    float: left;
    font-family: 'Righteous', cursive;
    margin-top: 200px;

    a {
        font-size: 35px;
        color: $h1-color;
        text-shadow: $title-shadow 1px 1px, $title-shadow 2px 2px, $title-shadow 3px 3px, $title-shadow 4px 4px;
    }
}
.story-logo {
margin-top: 1000px;
}

}
@import './mobile';

